/*! Navbar component */:root{--color-mainbg: #24242B;--color-black: #0C0C1E;--color-gray: #6B2E3C;--color-mainDark: #74112F;--color-mainLight: #F3E3E2}*,:before,:after{box-sizing:border-box}h1,h2,h3,h4,h5,h6{margin:0;font-weight:inherit}p,ul,ol{margin:0}ul,ol{list-style:none;padding-left:0}a{color:inherit;text-decoration:none}button{padding:0;border:none;font:inherit;color:inherit;background-color:rgba(0,0,0,0);cursor:pointer}img,video{max-width:100%;vertical-align:middle}img,video{height:auto}html{font-size:125%;overflow-x:hidden;scroll-behavior:smooth;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body{overflow-x:hidden;font-size:.8rem;font-weight:300;line-height:2;color:var(--color-mainLight);background-color:var(--color-mainbg)}.Navbar{display:flex;justify-content:space-around;align-items:center;background-color:rgba(0,0,0,0);margin-top:10px}.Navbar-title{z-index:10;margin-top:auto;margin-bottom:auto;text-transform:uppercase}@media screen and (max-width: 700px){.Navbar-list{z-index:1;position:absolute;top:0;right:100%;left:-100%;bottom:0;text-align:center;background-color:var(--color-black);transition:right .3s ease-in-out,left .3s ease-in-out}}@media screen and (min-width: 700px){.Navbar-list{position:relative;display:flex;align-items:center;height:100%}.Navbar-list::after{content:"";position:absolute;display:block;right:32px;bottom:5px;width:100px;height:5px;background-color:var(--color-mainDark);border-radius:10%;transition:right .4s ease-in-out,width .4s ease-in-out}.Navbar-list--Profil::after{right:444px !important;width:100px !important}.Navbar-list--active--Profil::after{right:444px}.Navbar-list--Compétences::after{right:276px !important;width:140px !important}.Navbar-list--active--Compétences::after{right:276px;width:140px}.Navbar-list--Projets::after{right:146px !important;width:100px !important}.Navbar-list--active--Projets::after{right:146px}}@media screen and (max-width: 700px){.Navbar-list.Navbar-list--shown{opacity:1;display:block;right:0;left:0;transition:right .3s ease-in-out,left .3s ease-in-out}}@media screen and (max-width: 700px){.Navbar-item:first-child{margin-top:50%}}@media screen and (max-width: 700px){.Navbar-item:not(:last-child){margin-bottom:5%}}.Navbar-item:last-child{background-color:var(--color-mainDark);border-radius:10%}.Navbar-item:last-child:hover{color:var(--color-black);background-color:#ef95b0;z-index:10}@media screen and (max-width: 700px){.Navbar-item:last-child{width:fit-content;margin-left:auto;margin-right:auto}}.Navbar-link{display:block;height:100%;font-size:1rem;padding:10px 20px}@media screen and (min-width: 700px){.Navbar-link{padding:10px 35px}}.Navbar-burger{z-index:10;display:flex;align-items:center;justify-content:center;width:40px;height:40px;border:3px solid var(--color-mainDark);border-radius:20%}@media screen and (min-width: 700px){.Navbar-burger{display:none}}.Navbar-burger--shown .Navbar-line{position:absolute}.Navbar-burger--shown .Navbar-line:first-child{top:8px;right:0;transform:rotate(135deg);transition:top .5s ease-in-out,transform .5s ease-in-out}.Navbar-burger--shown .Navbar-line:nth-child(2){margin-right:3px;top:50%;right:50%;transform:translate(50%, -50%);opacity:0;transition:opacity .3s ease-in-out}.Navbar-burger--shown .Navbar-line:last-child{bottom:8px;right:0;transform:rotate(-135deg);transition:bottom .5s ease-in-out,transform .5s ease-in-out}.Navbar-content{z-index:10;position:relative;display:flex;flex-direction:column;justify-content:space-between;width:16px;height:18px}.Navbar-line{position:relative;width:100%;height:2px;background-color:var(--color-mainDark)}.Navbar-line:first-child{top:0;transition:top .5s ease-in-out,transform .5s ease-in-out}.Navbar-line:nth-child(2){width:70%;opacity:1;transition:opacity .2s ease-in-out}.Navbar-line:last-child{bottom:0;transition:bottom .5s ease-in-out,transform .5s ease-in-out}